<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <main class="content container" v-if="productLoading || productLoadingFailed">
    <SpinnerBase v-if="productLoading" type="line" size="250" line-weight="10" />
    <div v-else-if="productLoadingFailed">
      <p class="content__title">Произошла ошибка: {{ productLoadingFailed }} </p>
      <button class="button button--primery" @click.prevent="loadProduct">Попробовать ещё раз</button>
    </div>
  </main>

  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main', query: { categoryId: category.id.toString() } }">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <ProductGallery :images="productImages" />

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <div class="item__row item__row--center">
              <CounterBase v-model.number="productQuantity" maxValue="99" />

              <b class="item__price">
                {{ product.price | formatPrice }}
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block" v-if="product.colors.length > 0">
                <legend class="form__legend">Цвет</legend>
                <ProductColors class="colors--black" :productColors="product.colors" v-model="currentProductColorId"
                :radio-group="product.id" />
              </fieldset>

              <fieldset class="form__block" v-if="sizes.length > 0">
                <legend class="form__legend">Размер</legend>
                <label class="form__label form__label--small form__label--select">
                  <select class="form__select" type="text" name="category" v-model="currentProductSizeId">
                    <option v-for="size in sizes" :value="size.id" :key="size.id">{{ size.title }}</option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button class="item__button button button--primery" type="submit" :disabled="productSending || (productAmount < 1)">
              <span v-if="!productSending">В корзину</span>
              <SpinnerBase v-if="productSending" size="25" />
            </button>
            <div v-if="productFailed">
              <p>{{ productFailed }}</p>
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link" :class="{ 'tabs__link--current': tabs.info }" :href="tabs.info?null:'#'" @click.prevent="tabsClick('info')">
              Информация о товаре
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" :class="{ 'tabs__link--current': tabs.delivery }" :href="tabs.delivery?null:'#'" @click.prevent="tabsClick('delivery')">
              Доставка и возврат
            </a>
          </li>
        </ul>

        <div class="item__content" v-show="tabs.info">
          {{ product.content }}
        </div>

        <div class="item__content" v-show="tabs.delivery">
          <h3>Доставка:</h3>

          <p>
            1. Курьерская доставка по Москве и Московской области – 290 ₽<br>
            2.Самовывоз из магазина. Список и адреса магазинов Вы можете посмотреть здесь<br>
          </p>

          <h3>Возврат:</h3>

          <p>
            Любой возврат должен быть осуществлен в течение Возвраты в магазине БЕСПЛАТНО.<br>
            Вы можете вернуть товары в любой магазин. Магазин должен быть расположен в стране, в которой Вы осуществили покупку.
            БЕСПЛАТНЫЙ возврат в Пункт выдачи заказов.<br>
            Для того чтобы вернуть товар в одном из наших Пунктов выдачи заказов, позвоните по телефону 8 800 600 90 09<br>
          </p>

        </div>
      </div>
    </section>
    <ModalBase :open.sync="productAdded">
      <h2>Товар добавлен в корзину</h2>
    </ModalBase>
  </main>
</template>

<script>
import { formatPrice } from '@/helpers/formating';
import CounterBase from '@/components/base/CounterBase.vue';
import ProductColors from '@/components/ProductColors.vue';
import ProductGallery from '@/components/ProductGallery.vue';
import SpinnerBase from '@/components/base/SpinnerBase.vue';
import ModalBase from '@/components/base/ModalBase.vue';
import axios from 'axios';
import { API } from '@/config';
import { mapActions } from 'vuex';

import noFoto from '@/assets/nofoto.png';
import noFoto1 from '@/assets/No_image.svg';
import noFoto2 from '@/assets/no_foto.png';

export default {
  name: 'ProductPage',
  data() {
    return {
      productAmount: 1,
      productColorId: null,
      productSizeId: null,

      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productSending: false,
      productAdded: false,
      productFailed: false,

      tabs: {
        info: true,
        delivery: false,
      },
    };
  },
  filters: { formatPrice },
  computed: {
    product() {
      return this.productData;
    },
    productImages() {
      let img = noFoto;
      const { colors } = this.product;
      const currentColor = colors.find((col) => col.color.id === this.currentProductColorId);
      if (currentColor && currentColor.gallery && currentColor.gallery.length > 0) img = currentColor.gallery[0].file.url;
      return [img, noFoto1, noFoto2];
    },
    category() {
      return this.product.category;
    },
    sizes() {
      return this.product.sizes;
    },
    currentProductColorId: {
      get() {
        let color = this.product.colors[0].color.id;
        if (this.$route.query.colorId) {
          const is = this.product.colors.some((elm) => elm.color.id === +this.$route.query.colorId);
          if (is) color = +this.$route.query.colorId;
        }
        return this.productColorId || color;
      },
      set(value) {
        this.productColorId = value;
      },
    },
    currentProductSizeId: {
      get() {
        return this.productSizeId || this.sizes[0].id;
      },
      set(value) {
        this.productSizeId = value;
      },
    },
    productQuantity: {
      get() {
        return this.productAmount || 1;
      },
      set(value) {
        if (Number.isNaN(+value)) this.productAmount = 1;
        else this.productAmount = value;
      },
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    addToCart() {
      if (this.productAmount < 1) return;
      this.productAdded = false;
      this.productFailed = false;
      this.productSending = true;

      this.addProductToCart(
        {
          productId: this.product.id, quantity: this.productQuantity, colorId: this.productColorId, sizeId: this.productSizeId,
        },
      ).then(() => {
        this.productAdded = true;
      }).catch((err) => {
        this.productFailed = err.message;
      }).finally(() => { this.productSending = false; });
    },
    tabsClick(name) {
      Object.keys(this.tabs).forEach((key) => {
        this.tabs[key] = false;
      });
      this.tabs[name] = true;
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      return axios.get(`${API}/products/${this.$route.params.id}`)
        .then((res) => {
          this.productData = res.data;
          this.productColorId = this.currentProductColorId;
          this.productSizeId = this.currentProductSizeId;
        })
        .catch((err) => { this.productLoadingFailed = err.response.data.error.message; })
        .finally(() => { this.productLoading = false; });
    },
  },
  components: {
    CounterBase,
    ProductColors,
    SpinnerBase,
    ProductGallery,
    ModalBase,
  },
  watch: {
    productColorId(value) {
      if (value && value !== +this.$route.query.colorId) {
        this.$router.replace({ query: { colorId: value } });
      }
    },
    '$route.params.id': {
      immediate: true,
      handler() {
        this.currentProductColorId = undefined;
        this.currentProductSizeId = null;
        this.loadProduct();
      },
    },
    // '$route.query.colorId': { handler(value) { this.currentProductColorId = value; } },
  },
};
</script>
