<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click="currentValue--">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="currentValue">

    <button type="button" aria-label="Добавить один товар" @click="currentValue++">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  model: { prop: 'counter', event: 'change' },
  props: ['counter', 'maxValue'],
  computed: {
    currentValue: {
      get() { return this.counter; },
      set(value) {
        if (Number.isNaN(+value)) {
          this.$emit('change', 1);
          return;
        }
        if ((value < 1) || (value > +this.maxValue)) return;
        this.$emit('change', value);
      },
    },
  },
};
</script>
