<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
<main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ formatQuantityName(countProducts, 'товар', '', 'а', 'ов') }}
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter v-bind.sync="filter" :perPage.sync="productsPerPage" />

      <section class="catalog">
        <SpinnerBase v-if="productsLoading" type="line" size="300" line-weight="10" />
        <div v-else-if="productsLoadingFailed"> Произошла ошибка: {{ productsLoadingFailed }} <button
            @click.prevent="loadProducts">Попробовать ещё раз</button></div>

        <ProductList v-else :products="products" />

        <PaginationBase v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import axios from 'axios';
import { API } from '@/config';
import { formatQuantityName } from '@/helpers/formating';
import PaginationBase from '@/components/base/PaginationBase.vue';
import ProductList from '@/components/ProductList.vue';
import SpinnerBase from '@/components/base/SpinnerBase.vue';
import ProductFilter from '@/components/ProductFilter.vue';

export default {
  name: 'MainPage',
  components: {
    PaginationBase, ProductList, SpinnerBase, ProductFilter,
  },
  data() {
    return {
      page: 1,
      productsPerPage: 6,
      filter: {
        priceFrom: 0,
        priceTo: 0,
        categoryId: null,
        colorIds: [],
        materialIds: [],
        seasonIds: [],
      },
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,

      columns: 3,
      height: '350px',
      width: '270px',
      rowGap: '65px',
    };
  },
  computed: {
    products() {
      return this.productsData?.items;
    },
    countProducts() {
      return this.productsData?.pagination?.total;
    },
  },
  methods: {
    formatQuantityName,
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      return axios.get(`${API}/products`, {
        params: {
          page: this.page,
          limit: this.productsPerPage,
          categoryId: this.filter.categoryId || undefined,
          minPrice: this.filter.priceFrom || undefined,
          maxPrice: this.filter.priceTo || undefined,
          materialIds: this.filter.materialIds || undefined,
          colorIds: this.filter.colorIds || undefined,
          seasonIds: this.filter.seasonIds || undefined,
        },
      }).then((res) => { this.productsData = res.data; })
        .catch((err) => { this.productsLoadingFailed = err.message; })
        .finally(() => { this.productsLoading = false; });
    },
  },
  watch: {
    page() { this.loadProducts(); },
    filter: {
      deep: true,
      handler(value) {
        console.log(value.categoryId);
        if (value.categoryId >= 0) {
          const id = value.categoryId;
          if (id !== +this.$route.query.categoryId) this.$router.replace({ query: { categoryId: id || undefined } });
        }
        this.loadProducts();
      },
    },
    productsPerPage(value) {
      this.loadProducts();
      if (value > 12) {
        this.columns = 4;
        this.width = '195px';
        this.height = '270px';
        this.rowGap = '40px';
      } else {
        this.columns = 3;
        this.width = '270px';
        this.height = '350px';
        this.rowGap = '65px';
      }
    },
  },
  created() {
    console.log('created');
    this.filter.categoryId = +this.$route.query.categoryId || 0;
  },
};
</script>

<style scope>
.catalog__list {
  grid-template-columns: repeat(v-bind(columns),v-bind(width));
  row-gap: v-bind(rowGap);
}
.catalog__pic {
    width: v-bind(width);
    height: v-bind(height);
}
.catalog__pic img {
    width: v-bind(width);
    height: v-bind(height);
}
</style>
