import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API } from '@/config';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    cartProducts: [],

    userAccessKey: null,

    cartProductsData: [],

    orderInfo: null,
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts;
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.price * item.quantity) + acc, 0);
    },
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProductsData = [];
      state.cartProducts = [];
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        _origin: item,
        basketItemId: item.id,
        price: item.price,
        quantity: item.quantity,
        color: item.color.color,
        image: item.color.gallery[0].file.url,
        productId: item.product.id,
        title: item.product.title,
        size: item.size.title,
      }));
    },
    updateCartItemAmount(state, { basketItemId, quantity }) {
      const item = state.cartProducts.find((elm) => elm.basketItemId === basketItemId);

      if (item) {
        item.quantity = quantity;
      }
    },
    deleteCartItem(state, basketItemId) {
      state.cartProducts = state.cartProducts.filter((p) => p.basketItemId !== basketItemId);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(`${API}/orders/${orderId}`, { params: { userAccessKey: context.state.userAccessKey } })
        .then((resp) => { context.commit('updateOrderInfo', resp.data); });
    },
    loadCart(context) {
      return axios.get(`${API}/baskets`, { params: { userAccessKey: context.state.userAccessKey || undefined } })
        .then((resp) => {
          if (!context.state.userAccessKey || context.state.userAccessKey !== resp.data.user.accessKey) {
            localStorage.setItem('userAccessKey', resp.data.user.accessKey);
            context.commit('updateUserAccessKey', resp.data.user.accessKey);
          }

          context.commit('updateCartProductsData', resp.data.items);
          context.commit('syncCartProducts');
        });
    },
    addProductToCart(context, {
      productId, colorId, sizeId, quantity,
    }) {
      return axios.post(`${API}/baskets/products`, {
        productId, colorId, sizeId, quantity,
      }, { params: { userAccessKey: context.state.userAccessKey } }).then((resp) => {
        context.commit('updateCartProductsData', resp.data.items);
        context.commit('syncCartProducts');
      });
    },
    updateCartProductAmount(context, { basketItemId, quantity }) {
      context.commit('updateCartItemAmount', { basketItemId, quantity });

      return axios.put(`${API}/baskets/products`, {
        basketItemId,
        quantity,
      }, {
        params: { userAccessKey: context.state.userAccessKey },
      }).then((resp) => {
        context.commit('updateCartProductsData', resp.data.items);
      }).catch(() => { context.commit('syncCartProducts'); });
    },
    deleteCartProduct(context, basketItemId) {
      context.commit('deleteCartItem', basketItemId);

      return axios.delete(`${API}/baskets/products`, {
        params: { userAccessKey: context.state.userAccessKey },
        data: { basketItemId },
      }).then((resp) => {
        context.commit('updateCartProductsData', resp.data.items);
      }).catch(() => { context.commit('syncCartProducts'); });
    },
  },
});

export default store;
