<template>
  <li class="catalog__item">
    <router-link class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id.toString() }, query: { colorId: currentProductColorId.toString() }}">
      <img :src="productImage" :alt="product.title">
    </router-link>
    <h3 class="catalog__title">
      <a>
        {{ product.title }}
      </a>
    </h3>
    <span class="catalog__price">
      {{ product.price | formatPrice }}
    </span>
    <ProductColors :productColors="product.colors" v-model="currentProductColorId" :radio-group="product.id"
      class="colors--black" v-if="colorsLength > 0" />
  </li>
</template>

<script>
import { formatPrice } from '@/helpers/formating';
import noFoto from '@/assets/nofoto.png';
import ProductColors from './ProductColors.vue';

export default {
  props: ['product'],
  data() { return { currentProductColorId: undefined }; },
  filters: { formatPrice },
  components: { ProductColors },
  computed: {
    colorsLength() { return this.product.colors.length; },
    productImage() {
      const { colors } = this.product;
      const currentColor = colors.find((col) => col.color.id === this.currentProductColorId);
      if (currentColor.gallery && currentColor.gallery.length > 0) return currentColor.gallery[0].file.url;
      return noFoto;
    },
  },
  created() {
    if (this.colorsLength > 0) this.currentProductColorId = this.product.colors[0].color.id;
  },
};
</script>
<style>
.catalog__pic img {
  object-fit: contain;
}
</style>
