<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.image" width="120" height="120" :alt="item.title">
    </div>
    <h3 class="product__title">
      {{ item.title }}
    </h3>

    <p class="product__info product__info--size">
      Размер: <span>{{ item.size }}</span>
    </p>

    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ 'background-color': item.color.code }"></i>
        {{ item.color.title }}
      </span>
    </p>

    <span class="product__code">
      Артикул: {{ item.productId }}
    </span>

    <CounterBase v-model.number="amount" max-value="99" class="product__counter" />

    <b class="product__price">
      {{ (item.price * item.quantity) | formatPrice }}
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.basketItemId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import CounterBase from '@/components/base/CounterBase.vue';
import { formatPrice } from '@/helpers/formating';
import { mapActions } from 'vuex';

export default {
  props: ['item'],
  components: { CounterBase },
  filters: { formatPrice },
  computed: {
    amount: {
      get() { return this.item.quantity; },
      set(value) {
        if (value < 1) return;
        this.$store.dispatch('updateCartProductAmount', { basketItemId: this.item.basketItemId, quantity: value });
      },
    },
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteCartProduct' }),
  },

};
</script>

<style scope>
.product {
  grid-template-rows: repeat(4, min-content);
}
.product__pic {
  grid-row:1/5;
}
.product__info--size {
  grid-row:2/3;
}
.product__info--color {
  grid-row:3/4;
}
.product__code {
  grid-row:4/5;
}
</style>
