<template>
  <div v-if="type === 'ping'" class="ping"></div>
  <div v-else-if="type === 'line'" class="line-wobble"></div>
  <svg v-else class="ring" viewBox="25 25 50 50" stroke-width="5"><circle cx="50" cy="50" r="20" /></svg>
</template>

<script>
export default {
  props: ['type', 'size', 'speed', 'color', 'lineWeight'],
  computed: {
    uiSize() { return `${this.size || 45}px`; },
    uiSpeed() { return `${this.speed || 2}s`; },
    uiColor() { return this.color || 'black'; },
    uiLineWeight() { return `${this.lineWeight || 5}px`; },
  },
};
</script>

<style>
.ping {
  position: relative;
  height: v-bind(uiSize);
  width: v-bind(uiSize);
}

.ping::before,
.ping::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  background-color: v-bind(uiColor);
  animation: pulse v-bind(uiSpeed) linear infinite;
  transform: scale(0);
  opacity: 0;
}

.ping::after {
  animation-delay: calc(v-bind(uiSpeed) / -2);
}

@keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

.line-wobble {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: v-bind(uiLineWeight);
  width: v-bind(uiSize);
  border-radius: calc(v-bind(uiLineWeight) / 2);
  overflow: hidden;
  transform: translate3d(0, 0, 0);
}

.line-wobble::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: v-bind(uiColor);
  opacity: 0.1;
}

.line-wobble::after {
  content: '';
  height: 100%;
  width: 100%;
  border-radius: calc(v-bind(uiLineWeight) / 2);
  animation: wobble v-bind(uiSpeed) ease-in-out infinite;
  transform: translateX(-95%);
  background-color: v-bind(uiColor);
}

@keyframes wobble {
  0%,
  100% {
    transform: translateX(-95%);
  }
  50% {
    transform: translateX(95%);
  }
}

.ring {
  height: v-bind(uiSize);
  width: v-bind(uiSize);
  vertical-align: middle;
  transform-origin: center;
  animation: rotate v-bind(uiSpeed) linear infinite;
}

.ring circle {
  fill: none;
  stroke: v-bind(uiColor);
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  stroke-linecap: round;
  animation: stretch calc(v-bind(uiSpeed) * 0.75) ease-in-out infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes stretch {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dashoffset: -124px;
  }
}
</style>
