export function formatNumber(number) {
  if (Number.isNaN(+number)) return 0;
  return new Intl.NumberFormat('ru').format(number);
}

export function formatPrice(price) {
  return `${formatNumber(price)} ₽`;
}

export function formatQuantityName(quan, origin, one, toFour, more, show = true) {
  let qName = origin;
  const nQuan = Number.isNaN(+quan) ? 0 : +quan;

  if (nQuan === 1) qName += one;
  else if (nQuan > 1 && nQuan < 5) qName += toFour;
  else qName += more;

  if (show) return `${nQuan} ${qName}`;
  return qName;
}
