<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <FormFieldBase :title="title" :error="error">
    <textarea v-if="isTextarea" class="form__input form__input--area" v-model="dataValue" :placeholder="placeholder"></textarea>
    <input v-else class="form__input" v-model="dataValue" :type="type" :placeholder="placeholder">
  </FormFieldBase>
</template>

<script>
import FormFieldBase from '@/components/base/FormFieldBase.vue';

export default {
  props: {
    type: { default: 'text' },
    textarea: { default: null },
    title: { default: '' },
    error: { default: '' },
    placeholder: { default: '' },
    value: { default: '' },
  },
  components: { FormFieldBase },
  computed: {
    dataValue: {
      get() { return this.value; },
      set(value) { this.$emit('input', value); },
    },
    isTextarea() { return (this.textarea !== null); },
  },
};
</script>
