<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <ul class="colors">
    <li class="colors__item" v-for="(col, index) in colors" :key="index">
      <label class="colors__label">
        <input class="colors__radio sr-only" type="radio" :name="`color-${radioGroup}`" :value="col.id" v-model="currentColor">
        <span class="colors__value" :style="{ 'background-color': col.code }" :title="col.title">
        </span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  model: { prop: 'currentColorId', event: 'change' },
  props: ['productColors', 'currentColorId', 'radioGroup'],
  computed: {
    colors() { return this.productColors.map((col) => col.color); },
    currentColor: {
      get() { return this.currentColorId; },
      set(value) { this.$emit('change', value); },
    },
  },
};
</script>
