<template>
  <ul class="catalog__pagination pagination" v-show="pages > 0">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === 1 }"
        aria-label="Предыдущая страница" @click.prevent="paginate(page - 1)" :href="(page === 1)?null:'#'">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link" :class="{ 'pagination__link--current': pageNumber === page }"
        :href="(pageNumber === page)?null:'#'" @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === pages }"
        :href="(page === pages)?null:'#'" aria-label="Следующая страница" @click.prevent="paginate(page + 1)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'perPage', 'count'],
  computed: {
    pages() {
      if (this.perPage <= 0 || !this.count) return 0;
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      if ((page < 1) || (page > this.pages)) return;
      this.$emit('paginate', page);
    },
  },
};
</script>
