<template>
<div class="item__pics pics">
  <div class="pics__wrapper">
    <img width="570" height="570" :src="currentImage" alt="Большая картинка товара">
  </div>
  <ul class="pics__list">
    <li class="pics__item" v-for="(img, index) in images" :key="index">
      <a href="#" class="pics__link" :class="{'pics__link--current': imageId === index}" @click.prevent="setCurrentImage(index)" >
        <img width="98" height="98" :src="img" alt="Маленькая картинка товара">
      </a>
    </li>
  </ul>
</div>
</template>

<script>
import noFoto from '@/assets/nofoto.png';

export default {
  name: 'ProductGallery',
  props: ['images'],
  data() {
    return {
      currentId: null,
    };
  },
  computed: {
    imageId() {
      if (this.currentId > 0 && this.currentId < this.images.length) return this.currentId;
      return 0;
    },
    currentImage() {
      if (!this.images) return noFoto;
      return this.images[this.imageId] || noFoto;
    },
  },
  methods: {
    setCurrentImage(id) {
      if (id >= 0 && id < this.images.length) this.currentId = id;
    },
  },
};
</script>
