<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="ordering">
        <div class="cart__field">
          <FieldsBase v-model="formData" :formError="formError" />

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <SpinnerBase v-if="deliveriesState.loading" />
            <div v-else-if="deliveriesState.error" >{{ deliveriesState.error }}</div>
            <ul v-else class="cart__options options">
              <li class="options__item" v-for="d in deliveries" :key="d.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" :value="d.id" v-model="currentDeliveryId">
                  <span class="options__value">
                    {{ d.title }} <b>{{ d.price | formatPrice }}</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <SpinnerBase v-if="paymentsState.loading" />
            <div v-else-if="paymentsState.error" >{{ paymentsState.error }}</div>
            <ul class="cart__options options">
              <li class="options__item" v-for="p in payments" :key="p.id" >
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" :value="p.id" v-model="currentPaymentId">
                  <span class="options__value">
                    {{ p.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="item in products" :key="item.basketItemId" :item="item" />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{ deliveryPrice | formatPrice }}</b></p>
            <p>Итого: <b>{{ productsLength }}</b> {{ formatQuantityName(productsLength, 'товар', '', 'а', 'ов', false) }}
              на сумму <b>{{ fullPrice | formatPrice }}</b>
            </p>
          </div>

          <button v-show="productsLength > 0" class="cart__button button button--primery" type="submit"
            :disabled="orderSending">
            <SpinnerBase v-if="orderSending" />
            <span v-else>Оформить заказ</span>
          </button>
        </div>
        <div class="cart__error form__error-block" v-show="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import FieldsBase from '@/components/base/FieldsBase.vue';
import { formatPrice, formatQuantityName } from '@/helpers/formating';
import { mapGetters } from 'vuex';
import OrderItem from '@/components/OrderItem.vue';
import axios from 'axios';
import { API } from '@/config';
import SpinnerBase from '@/components/base/SpinnerBase.vue';

export default {
  name: 'OrderPage',
  components: { FieldsBase, OrderItem, SpinnerBase },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderSending: false,

      deliveryTypeId: null,
      deliveryPrice: 0,

      deliveriesState: { loading: true, error: false },
      deliveriesData: null,

      paymentId: null,
      paymentsState: { loading: true, error: false },
      paymentsData: null,
    };
  },
  filters: { formatPrice },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    productsLength() { return this.products.length; },
    deliveries() { return this.deliveriesData; },
    payments() { return this.paymentsData; },
    currentDeliveryId: {
      get() { return this.deliveryTypeId; },
      set(value) { this.deliveryTypeId = value; },
    },
    currentPaymentId: {
      get() { return this.paymentId; },
      set(value) { this.paymentId = value; },
    },
    fullPrice() { return this.totalPrice + this.deliveryPrice; },
  },
  methods: {
    formatQuantityName,
    ordering() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderSending = true;

      return axios.post(`${API}/orders`, { ...this.formData, deliveryTypeId: this.deliveryTypeId, paymentTypeId: this.paymentId }, { params: { userAccessKey: this.$store.state.userAccessKey } })
        .then((resp) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', resp.data);
          this.$router.push({ name: 'orderInfo', params: { id: resp.data.id } });
        }).catch((err) => {
          this.formError = err.response.data.error.request || {};
          this.formErrorMessage = err.response.data.error.message || '';
        }).finally(() => { this.orderSending = false; });
    },
    loadDeliveries() {
      this.deliveriesState.loading = true;
      this.deliveriesState.error = false;
      return axios.get(`${API}/deliveries`)
        .then((res) => {
          this.deliveriesData = res.data;
          this.currentDeliveryId = this.deliveries[0].id;
        })
        .catch((err) => { this.deliveriesState.error = err.message; })
        .finally(() => { this.deliveriesState.loading = false; });
    },
    loadPayments(id) {
      this.paymentsState.loading = true;
      this.paymentsState.error = false;
      return axios.get(`${API}/payments?deliveryTypeId=${id}`)
        .then((res) => {
          this.paymentsData = res.data;
          this.currentPaymentId = this.payments[0].id;
        })
        .catch((err) => { this.paymentsState.error = err.message; })
        .finally(() => { this.paymentsState.loading = false; });
    },
  },
  watch: {
    currentDeliveryId() {
      this.deliveryPrice = +this.deliveries.find((elm) => elm.id === this.deliveryTypeId).price;
      this.loadPayments(this.deliveryTypeId);
    },
  },
  created() { this.loadDeliveries(); },
};
</script>
