<template>
  <router-link class="header__cart" aria-label="Корзина с товарами" :to="{ name: 'cart' }">
    <svg width="19" height="24">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span ref="counter" class="header__count" aria-label="Количество товаров">{{ productsLength }}</span>
  </router-link>
</template>

<script>
const anim = [
  { transform: 'scale(5)' },
  { transform: 'scale(1)' },
];

const timing = {
  duration: 1500,
  iterations: 1,
};

export default {
  name: 'cartIndicator',
  computed: {
    productsLength() {
      return this.$store.state.cartProducts.length;
    },
  },
  watch: {
    productsLength() {
      this.$refs.counter.animate(anim, timing);
    },
  },
};
</script>
