<template>
  <main class="content container" v-if="orderInfoLoading || orderInfoFailed" >
    <SpinnerBase v-if="orderInfoLoading" type="line" size="300" line-weight="10" />
    <div v-else-if="orderInfoFailed">
      <p class="content__title">Произошла ошибка: {{ orderInfoFailed }} </p>
      <button class="button button--primery" @click.prevent="loadingOrderInfo">Попробовать ещё раз</button>
    </div>
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <h1 class="content__title">
        Заказ оформлен <span>№ {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                {{ orderInfo.paymentType }}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="item in products" :key="item.basketItemId" :item="item" />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{ orderInfo.deliveryType.title }}</b></p>
            <p>Итого: <b>{{ productsLength }}</b> {{ formatQuantityName(productsLength, 'товар', '', 'а', 'ов', false)
              }} на сумму <b>{{ orderInfo.totalPrice | formatPrice }}</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import OrderItem from '@/components/OrderItem.vue';
import SpinnerBase from '@/components/base/SpinnerBase.vue';
import { formatPrice, formatQuantityName } from '@/helpers/formating';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      orderInfoLoading: true,
      orderInfoFailed: false,
    };
  },
  filters: { formatPrice },
  methods: {
    ...mapActions(['loadOrderInfo']),
    formatQuantityName,
    loadingOrderInfo() {
      this.orderInfoLoading = true;
      this.orderInfoFailed = false;
      this.loadOrderInfo(this.$route.params.id)
        .catch((err) => { this.orderInfoFailed = err.response.data.error.message; })
        .finally(() => { this.orderInfoLoading = false; });
    },
  },
  computed: {
    orderInfo() { return this.$store.state.orderInfo || {}; },
    products() {
      return this.orderInfo.basket?.items.map((item) => ({
        basketItemId: item.id,
        price: item.price,
        quantity: item.quantity,
        productId: item.product.id,
        title: item.product.title,
      }));
    },
    productsLength() { return this.products?.length || 0; },
  },
  watch: {
    '$route.params.id': {
      immediate: true,
      handler() {
        if (this.$store.state.orderInfo && (this.$store.state.orderInfo?.id === this.$route.params.id)) { this.orderInfoLoading = false; return; }
        this.loadingOrderInfo();
      },
    },
  },
  components: { OrderItem, SpinnerBase },
};
</script>
