<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit()">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="number" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="number" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <SpinnerBase v-if="categoriesLoad" type="line" size="100" class="form__block" />
      <fieldset v-else-if="categories" class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model="currentCategoryId">
            <option value="0">Все категории</option>
            <option v-for="cat in categories" :value="cat.id" :key="cat.id">{{ cat.title }}</option>
          </select>
        </label>
      </fieldset>

      <SpinnerBase v-if="colorsLoad" type="line" size="100" class="form__block" />
      <fieldset v-else-if="colors" class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors colors--black">
          <li class="colors__item" v-for="col in colors" :key="col.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="checkbox" name="color" :value="col.id" v-model="currentColorIds">
              <span class="colors__value" :style="{'background-color': col.code }" :title="col.title">
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <SpinnerBase v-if="materialsLoad" type="line" size="100" class="form__block" />
      <fieldset v-else-if="materials" class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="mat in materials" :key="mat.code">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="material" :value="mat.id" v-model="currentMaterialIds">
              <span class="check-list__desc">
                {{ mat.title }}
                <span>({{ mat.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <SpinnerBase v-if="seasonsLoad" type="line" size="100" class="form__block" />
      <fieldset v-else-if="seasons" class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="seas in seasons" :key="seas.code">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="collection" :value="seas.id" v-model="currentSeasonIds">
              <span class="check-list__desc">
                {{ seas.title }}
                <span>({{ seas.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Товаров на странице</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model="currentPerPage">
            <option value="6">6</option>
            <option value="9">9</option>
            <option value="12">12</option>
            <option value="16">16</option>
            <option value="24">24</option>
            <option value="32">32</option>
          </select>
        </label>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button v-show="needReset === true" class="filter__reset button button--second" type="button" @click.prevent="submit(true)">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import { API } from '@/config';
import axios from 'axios';
import SpinnerBase from './base/SpinnerBase.vue';

const defaultPerPage = 6;

export default {
  name: 'ProductFilter',
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorIds', 'materialIds', 'seasonIds', 'perPage'],
  components: { SpinnerBase },
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColorIds: [],
      currentMaterialIds: [],
      currentSeasonIds: [],
      currentPerPage: defaultPerPage,

      categoriesData: null,
      colorsData: null,
      materialsData: null,
      seasonsData: null,

      categoriesLoad: true,
      colorsLoad: true,
      materialsLoad: true,
      seasonsLoad: true,
    };
  },
  computed: {
    categories() { return this.categoriesData?.items; },
    colors() { return this.colorsData?.items; },
    materials() { return this.materialsData?.items; },
    seasons() { return this.seasonsData?.items; },
    needReset() {
      return !!(
        this.currentPriceFrom || this.currentPriceTo || this.currentCategoryId
        || this.currentColorIds.length > 0
        || this.currentMaterialIds.length > 0
        || this.currentSeasonIds.length > 0
      );
    },
  },
  watch: {
    priceFrom(value) { this.currentPriceFrom = value; },
    priceTo(value) { this.currentPriceTo = value; },
    categoryId: { immediate: true, handler(value) { this.currentCategoryId = value; } },
    colorIds(value) { this.currentColorIds = value; },
    materialIds(value) { this.currentMaterialIds = value; },
    seasonIds(value) { this.currentSeasonIds = value; },
    perPage(value) { this.currentPerPage = value; },
  },
  methods: {
    submit(reset = false) {
      this.$emit('update:priceFrom', (reset) ? 0 : this.currentPriceFrom);
      this.$emit('update:priceTo', (reset) ? 0 : this.currentPriceTo);
      this.$emit('update:categoryId', (reset) ? 0 : this.currentCategoryId);
      this.$emit('update:colorIds', (reset) ? [] : this.currentColorIds);
      this.$emit('update:materialIds', (reset) ? [] : this.currentMaterialIds);
      this.$emit('update:seasonIds', (reset) ? [] : this.currentSeasonIds);
      this.$emit('update:perPage', (reset) ? defaultPerPage : this.currentPerPage);
    },
    loadCategories() {
      this.categoriesLoad = true;
      return axios.get(`${API}/productCategories`)
        .then((r) => { this.categoriesData = r.data; })
        .finally(() => { this.categoriesLoad = false; });
    },
    loadColors() {
      this.colorsLoad = true;
      return axios.get(`${API}/colors`)
        .then((r) => { this.colorsData = r.data; })
        .finally(() => { this.colorsLoad = false; });
    },
    loadMaterials() {
      this.materialsLoad = true;
      return axios.get(`${API}/materials`)
        .then((r) => { this.materialsData = r.data; })
        .finally(() => { this.materialsLoad = false; });
    },
    loadSeasons() {
      this.seasonsLoad = true;
      return axios.get(`${API}/seasons`)
        .then((r) => { this.seasonsData = r.data; })
        .finally(() => { this.seasonsLoad = false; });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
    this.loadMaterials();
    this.loadSeasons();
  },
};
</script>
