<template>
<article v-if="open">
  <div class="blackout"></div>
  <div class="modal" @click="onOutsideClick" @keyup.esc="doClose">
    <div ref="content" class="modal__content">
      <slot></slot>
      <button type="button" class="modal__close" @click.prevent="doClose">X</button>
    </div>
  </div>
</article>
</template>

<script>
let openedCount = 0;
let atleastOneOpen = false;

export default {
  name: 'baseModal',
  props: { open: { default: false } },
  methods: {
    doClose() { this.$emit('update:open', false); },
    onOutsideClick($event) {
      if ($event.target !== this.$refs.content && $event.target.contains(this.$refs.content)) this.doClose();
    },
    checkBlackoutState() {
      if (!openedCount) {
        atleastOneOpen = false;
        document.body.style.overflow = null;
        document.body.style.paddingRight = null;
      } else if (!atleastOneOpen && openedCount === 1) {
        atleastOneOpen = true;
        document.body.style.paddingRight = `${window.innerWidth - document.documentElement.clientWidth}px`;
        document.body.style.overflow = 'hidden';
      }
    },
  },
  watch: {
    open: {
      immediate: true,
      handler(value) {
        if (value) openedCount += 1;
        else if (openedCount > 0) openedCount -= 1;

        this.checkBlackoutState();
      },
    },
  },
};
</script>

<style scoped>
.blackout {
  z-index: 1010;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.75;
  background-color: #000;
}
.modal {
  z-index: 1010;
  overflow: auto;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
}
.modal__content {
  position: relative;
  max-width: 1000px;
  margin-top: auto;
  margin-bottom: auto;
  padding: 40px;
  background-color: #fff;
}
.modal__close {
  position: absolute;
  top: -1.25em;
  right: -1.25em;
  padding: 10px;
  line-height: 1;
}
</style>
